.App {
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: center;
}
.AppSVG {
    width: 100vw;
}
body {
    background-color: rgb(216, 178, 255);
}
@font-face {
    font-family: Chimpsdale;
    src: url('../fonts/Chimpsdale.otf');
}
.dealBtn {
    display: block;
    
    font-size: 20px;
    padding: 10px 20px;
    border: none;
    border-radius: 40px;
    background-color: purple;
    color: white;
    font-family: Chimpsdale;
    transition: all 0.3s ease 0s;
}
.dealBtn:hover {
    color: purple;
    background-color: white;
    border: 1px solid purple;
    transition: all 0.1s ease 0s;
}
.dealBtn:active {
    background-color: purple;
    color: white;
    transition: all 0s ease 0s;
}

.buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 0 auto;
    position: relative;
}
#shuffle {
    visibility: hidden;
}
#counter {
    position: absolute;
    display: block;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 30px;
    width: 100%;
    text-align: center;
    
}
.layout {
    display: flex;
    flex-direction: column;
    gap: 20px;
}